body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Game {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 600px;
    height: 600px;
}

.Game .Game__cell {
    background: #ececec;
    flex: 0 0 25%;
    align-items: stretch;
    text-align: center;
    font-size: 32px;
    line-height: 150px; /* TODO: make flexible */
}

.Game .Game__cell.empty {
    background: #fff;
}

.Game .Game__cell:hover {
    background: #e0e0e0;
    cursor: pointer;
}

.Game .Game__cell.empty:hover {
    background: #fff;
    cursor: default;
}

